.nav {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    height: 70px;
    transition: all 0.5s ease;
}
#navscroll {
    background: #14171A;
}
.nav .logo h1{
    margin-left: 30px;
    font-size: 40px;
    font-weight: 900;
    margin-top: -3px;
    color: #1DA1F2 ;
    cursor: default;
}
.nav .logo h1 span{
    font-size: 50px;
    font-weight: 900;
}

.nav .menu .lists {
    margin-right: 50px;
    display: flex;
    justify-content: center;
    gap: 30px;
}

.nav .menu .lists a , .nav .menumobile .lists a{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 24px;
    font-weight: bold;
    color: #1DA1F2 ;
}

.nav .menu .lists a:hover, nav .menumobile .lists a:hover{
    opacity: 0.8;
}

/* responsive menu */

.nav .menumobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    align-content: center;
    background : #14171A;
}

.nav .hidden {
    visibility: hidden;
}
.nav .bar {
    position: fixed;
    top: 12px;
    right: 40px;
    color: #1DA1F2;
    font-size: 35px;
    font-weight: 300;

}
.nav .nobar {
    visibility: hidden;
}
.nav .menumobile .lists {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    gap: 30px;
}

.nav .menumobile .toggle {
    position: fixed;
    top: 12px;
    right: 40px;
    font-size: 35px;
    font-weight: 600;
    color: #1DA1F2;
}

.nav .menu .lists .social {
    padding-left: 20px;
}
.nav .menu .lists .social, .nav .menumobile .lists .social{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.nav .menu .lists .social a, .nav .menumobile .lists .social a{
    color: #E1E8ED;
    font-size: 19px;
}

.nav .menu .lists button a {
    color: #333;
}
.nav .menumobile .lists button a {
    color: #E1E8ED;
}
.nav button {
    font-size: 18px;
    font-weight: bold;
    background: #1DA1F2;
    padding: 8px 16px;
}

.nav .menumobile button {
   background: #1DA1F2;
}

/*  for responsive */

@media screen and (max-width: 950px) {
    .nav .logo h1{
        margin-left: -100px;
        font-size: 36px;
        font-weight: 900;
        margin-top: -3px;
        color: #1DA1F2 ;
        cursor: default;
    }
    .nav .logo h1 span{
        font-size: 42px;
        font-weight: 900;
    }
  }