.service {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    color: #000;
    gap: 10px;
}

.service .titlelarge {
    margin-top: 50px;
}

.service .service-cards {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 4px;
}
.service .service-cards .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    width: 250px;
}

.service .service-cards .content .icon {
    font-size: 80px;
    color: #1DA1F2;
}

.service .service-cards .content .title h1{
    font-size: 32px;
    color: #555;
}

.service .service-cards .content .paragraph p{
    font-size: 21px;
    font-weight: 300;
}