.contact {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #000;
    background: #E1E8ED;
    border-top: 2px solid #1DA1F2;
    padding-bottom: 30px;
}

.contact .title {
    margin-top: 30px;
}

.contact .title h1 a{
    color: #1DA1F2;
}

.contact-sec .personel-info {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 10px;
    padding: 12px;
}

.contact-sec .personel-info .name {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}

.contact-sec .personel-info h3 {
    font-size: 21px;
    font-weight: 400;
}
.contact-sec .personel-info .name span{
    font-size: 28px;
    font-weight: 500;
    color: #1DA1F2;
    margin-top:15px;
    padding: 0px 10px;
}