.home {
  position: relative;
  height: 100vh;  
  box-sizing: border-box;
  padding: 0;
  display: flex;
  justify-content: center;
  color: #E1E8ED ;
}

.home .content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.home .content h2{
  font-size: 42px;
  font-weight: bold;
}
.home .content h2 #dot{
  font-size: 52px;
  font-weight: bold;
}
.home .content h3{
  margin-top: -20px;
  font-size: 28px;
  font-weight: bold;
}

.home .content h2 span, .home .content h3 span{
  color: #1DA1F2;
}


.home .content p {
  font-size: 32px;
  font-weight: 500;
}

.home .content p span{
  color: #1DA1F2;
  font-size: 38px;
}

.home .content button a {
  color: #E1E8ED;
}

.home .content button{
  font-size: 24px;
  font-weight: bold;
  background: #1DA1F2;
  color: #E1E8ED;
  padding: 8px 16px;
}
.home .content button:hover{
  opacity: 0.8;
}

@media screen and (max-width: 950px) {
  .home{
    height: 100vh;
    background: none;
  }
  .home .content h2{
    font-size: 24px;
  }
  .home .content h3{
    font-size: 21px;
  }
}