
.app {
    position: relative;
    top: 0;
    left: 0;
    overflow: hidden;
    letter-spacing: 1px;
    background: #14171A;
}

.about-skill {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 50px;
    background: #14171A;
    border-top: 2px solid #1DA1F2;

}
.app .service-sec {
    position: relative;
    background: #E1E8ED;
    display: flex;
    justify-content: center;
    border-bottom: 3px solid #1DA1F2;
}
